<template>
  <rs-layout>
    <rs-breadcrumb />
    <rs-card>
      <template #header>
        <div class="flex">
          <vue-feather
            class="mr-2 flex justify-center"
            type="info"
          ></vue-feather
          >Info
        </div>
      </template>
      <template #body>
        <p class="mb-4">
          This template uses
          <a
            href="https://formkit.com"
            target="_blank"
            class="hover:underline text-rose-400"
            >Formkit</a
          >
          which is a powerfull form builder for Vue. It is a Vue component that
          allows you to create forms with a lot of different elements.
        </p>
      </template>
    </rs-card>

    <rs-card>
      <template #header>Default</template>
      <template #body>
        <FormKit type="text" label="Default" />
        <FormKit type="text">
          <template #label>
            <label class="font-semibold"
              >Required <span class="text-red-400">*</span></label
            >
          </template>
        </FormKit>
        <FormKit
          type="text"
          label="Placeholder"
          placeholder="This is a placeholder"
        />
      </template>
    </rs-card>

  </rs-layout>
</template>

<script>
import { ref, computed } from "vue";
import RsCard from "@/components/Card.vue";

export default {
  name: "form-element-input",
  components: {
    RsCard,
  },
  setup() {
    const key = ref("jg9dsk23rnfsd");

    const generateKey = () => {
      key.value = Math.random().toString(36).substring(2, 15);
    };

    const computedKey = computed(() => {
      return key.value;
    });

    return {
      generateKey,
      computedKey,
    };
  },
};
</script>

<style lang="scss" scoped></style>
